.container {
  padding: 0.2em;
}

.container.grid {
  width: 50%;
}

.container.list {
  width: 100%;
}

.video {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  border: 1px solid rgb(222, 222, 222);
  border-radius: 6px;
  box-shadow: 3px 3px 5px 0px rgba(191, 191, 191, 0.53);
  cursor: pointer;
  transition: transform 250ms ease-in;
}

.video:hover {
  transform: scale(1.02);
}

.thumbnail {
  width: 40%;
  height: 100%;
}

.metadata {
  margin-left: 0.2em;
}

.title {
  font-size: 0.9rem;
  font-weight: 500;
}

.title,
.channel {
  margin: 0;
  padding: 0.3em;
}

.channel {
  font-size: 0.8rem;
  color: rgb(53, 53, 53);
}